___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

:root {
  --primary-color: #8b4513;
  --secondary-color: #d2b48c;
  --background-color: #f5f5dc;
  --error-color: #a52a2a;
  --text-color: #3e2723;
  --accent-color: #5d4037;
  --border-color: #382218;
  --box-shadow: 6px 6px 0 rgba(56, 34, 24, 0.8);
  --hover-shadow: 4px 4px 0 rgba(56, 34, 24, 0.8);
  --border-radius: 2px;
  --border-width: 3px;
}

body {
  background-color: var(--background-color);
  padding: 20px;
  color: var(--text-color);
}

app-title {
  display: block;
  text-align: center;
  margin-bottom: 30px;
}

app-title h1 {
  color: var(--text-color);
  font-size: 3rem;
  font-weight: 800;
  padding: 10px 20px;
  display: inline-block;
  background-color: var(--secondary-color);
  border: var(--border-width) solid var(--border-color);
  box-shadow: var(--box-shadow);
  text-transform: uppercase;
  letter-spacing: 1px;
}

.highlight {
  color: var(--primary-color);
  position: relative;
  display: inline-block;
  transform: rotate(-2deg);
}

.main-container {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  gap: 30px;
}

.form-section {
  flex: 1;
  max-width: 350px;
}

.notes-section {
  flex: 3;
  height: calc(100vh - 140px);
  display: flex;
  flex-direction: column;
}

note-form {
  display: block;
  background-color: white;
  padding: 25px;
  border: var(--border-width) solid var(--border-color);
  box-shadow: var(--box-shadow);
  margin-bottom: 30px;
  transition: transform 0.3s ease;
}

note-form:hover {
  transform: translateY(-5px);
}

.form-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.validation-message {
  color: var(--error-color);
  font-size: 0.8rem;
  font-weight: 600;
  margin-top: 3px;
  display: none;
}

.form-group[data-error-type="required"] .validation-message[data-error="required"] {
  display: block;
}

.form-group[data-error-type="minlength"] .validation-message[data-error="minlength"] {
  display: block;
}

.form-group.error input,
.form-group.error textarea {
  border-color: var(--error-color);
  border-width: var(--border-width);
  background-color: rgba(255, 43, 43, 0.1);
}

label {
  font-weight: 600;
  color: var(--text-color);
  font-size: 1.1rem;
}

input[type="text"],
textarea {
  padding: 12px;
  border: var(--border-width) solid var(--border-color);
  border-radius: var(--border-radius);
  font-size: 1rem;
  font-family: "Poppins", sans-serif;
  background-color: white;
  transition: all 0.2s ease;
}

input[type="text"]:focus,
textarea:focus {
  outline: none;
  box-shadow: var(--hover-shadow);
  transform: translateY(-2px);
}

textarea {
  min-height: 120px;
  resize: vertical;
}

button {
  background-color: var(--primary-color);
  color: white;
  border: var(--border-width) solid var(--border-color);
  padding: 12px 20px;
  border-radius: var(--border-radius);
  cursor: pointer;
  font-size: 1rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
  align-self: flex-start;
  box-shadow: var(--box-shadow);
  transition: all 0.2s ease;
  position: relative;
  overflow: hidden;
}

button:hover {
  transform: translateY(-2px);
  box-shadow: var(--hover-shadow);
}

button:active {
  transform: translateY(0);
  box-shadow: 2px 2px 0 rgba(0, 0, 0, 0.9);
  animation: pop 0.3s ease;
}

button::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.2);
  transform: translateX(-100%);
  transition: transform 0.3s ease;
}

button:hover::after {
  transform: translateX(0);
}

.notes-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  background-color: var(--background-color);
  padding: 15px;
  z-index: 10;
  margin-bottom: 20px;
  flex-wrap: wrap;
  gap: 15px;
  border: var(--border-width) solid var(--border-color);
  box-shadow: var(--box-shadow);
}

.notes-section-title {
  color: var(--text-color);
  font-size: 1.8rem;
  font-weight: 800;
  margin-bottom: 0;
  text-transform: uppercase;
}

.search-container {
  display: flex;
  align-items: center;
}

#search-notes {
  padding: 12px;
  border: var(--border-width) solid var(--border-color);
  border-radius: var(--border-radius);
  font-size: 1rem;
  width: 250px;
  outline: none;
  font-family: "Poppins", sans-serif;
  box-shadow: 4px 4px 0 rgba(0, 0, 0, 0.7);
  transition: all 0.2s ease;
}

#search-notes:focus {
  transform: translateY(-2px);
  box-shadow: var(--hover-shadow);
}

notes-list {
  display: block;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.notes-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 25px;
  overflow-y: auto;
  padding-right: 15px;
  padding-bottom: 20px;
  flex-grow: 1;
}

.note {
  background-color: var(--secondary-color);
  border: var(--border-width) solid var(--border-color);
  border-radius: var(--border-radius);
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 15px;
  box-shadow: var(--box-shadow);
  transition: all 0.3s ease;
  transform-origin: center;
  animation: fadeIn 0.5s ease forwards;
}

.note:hover {
  transform: translateY(-5px) rotate(1deg);
  box-shadow: var(--hover-shadow);
}

.note:active {
  animation: wobble 0.8s ease;
}

.note-title {
  font-weight: 700;
  font-size: 1.3rem;
  color: var(--text-color);
  border-bottom: var(--border-width) solid var(--border-color);
  padding-bottom: 12px;
  text-transform: uppercase;
}

.note-body {
  color: var(--text-color);
  flex-grow: 1;
  font-size: 1.05rem;
  line-height: 1.6;
}

.note-footer {
  display: flex;
  justify-content: flex-end;
  gap: 15px;
}

.delete-btn {
  background-color: var(--error-color);
  padding: 8px 15px;
  font-size: 0.9rem;
}

.archive-btn,
.unarchive-btn {
  background-color: var(--accent-color);
  padding: 8px 15px;
  font-size: 0.9rem;
}

.delete-btn:hover,
.archive-btn:hover,
.unarchive-btn:hover {
  opacity: 1;
}

.view-controls {
  display: flex;
  gap: 15px;
}

.view-btn {
  background-color: white;
  color: var(--text-color);
  border: var(--border-width) solid var(--border-color);
  font-size: 1rem;
  padding: 10px 15px;
  box-shadow: 4px 4px 0 rgba(56, 34, 24, 0.7);
}

.view-btn.active {
  background-color: var(--accent-color);
  color: white;
  box-shadow: var(--box-shadow);
}

/* loading indicator */
.loading-container {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.95);
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 1000;
}

.loading-container.visible {
  display: flex;
}

.loading-box {
  position: relative;
  background-color: white;
  padding: 40px;
  border: var(--border-width) solid var(--border-color);
  box-shadow: var(--box-shadow);
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: shake 0.5s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  animation-delay: 0.5s;
  animation-iteration-count: infinite;
}

.loading-spinner {
  width: 70px;
  height: 70px;
  border: 8px solid var(--secondary-color);
  border-top: 8px solid var(--primary-color);
  border-right: 8px solid var(--accent-color);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 20px;
  box-shadow: 4px 4px 0 rgba(56, 34, 24, 0.7);
}

.loading-text {
  color: var(--text-color);
  font-size: 24px;
  font-weight: 800;
  text-transform: uppercase;
  background-color: var(--secondary-color);
  padding: 10px 20px;
  border: var(--border-width) solid var(--border-color);
  box-shadow: 4px 4px 0 rgba(56, 34, 24, 0.7);
  animation: pulse 2s infinite;
}

.loading-decoration {
  position: absolute;
  width: 30px;
  height: 30px;
  background-color: var(--secondary-color);
  border: var(--border-width) solid var(--border-color);
  top: -15px;
  right: -15px;
  transform: rotate(15deg);
  animation: rotateDecoration 4s linear infinite;
}

.loading-decoration-2 {
  width: 25px;
  height: 25px;
  background-color: var(--primary-color);
  bottom: -10px;
  left: -10px;
  top: auto;
  right: auto;
  animation-duration: 5s;
  animation-direction: reverse;
}

.loading-decoration-3 {
  width: 20px;
  height: 20px;
  background-color: var(--accent-color);
  top: -10px;
  left: -10px;
  right: auto;
  animation-duration: 3s;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes pulse {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

@keyframes rotateDecoration {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes shake {
  0%,
  100% {
    transform: translateX(0);
  }
  10%,
  30%,
  50%,
  70%,
  90% {
    transform: translateX(-2px);
  }
  20%,
  40%,
  60%,
  80% {
    transform: translateX(2px);
  }
}

/* animation untuk notes */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes pop {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes wobble {
  0% {
    transform: translateX(0) rotate(0);
  }
  15% {
    transform: translateX(-5px) rotate(-5deg);
  }
  30% {
    transform: translateX(4px) rotate(3deg);
  }
  45% {
    transform: translateX(-3px) rotate(-2deg);
  }
  60% {
    transform: translateX(2px) rotate(1deg);
  }
  75% {
    transform: translateX(-1px) rotate(-1deg);
  }
  100% {
    transform: translateX(0) rotate(0);
  }
}

.note {
  animation: fadeIn 0.5s ease forwards;
}

.note:hover {
  transform: translateY(-5px) rotate(1deg);
  box-shadow: var(--hover-shadow);
}

.note:active {
  animation: wobble 0.8s ease;
}

/* animation delay */
.note:nth-child(1) {
  animation-delay: 0.1s;
}
.note:nth-child(2) {
  animation-delay: 0.2s;
}
.note:nth-child(3) {
  animation-delay: 0.3s;
}
.note:nth-child(4) {
  animation-delay: 0.4s;
}
.note:nth-child(5) {
  animation-delay: 0.5s;
}
.note:nth-child(6) {
  animation-delay: 0.6s;
}

/* responsive */
@media screen and (max-width: 768px) {
  .main-container {
    flex-direction: column;
  }

  .form-section {
    max-width: 100%;
  }

  .notes-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
    padding: 20px;
  }

  .view-controls {
    width: 100%;
    display: flex;
  }

  .view-btn {
    flex: 1;
    text-align: center;
  }

  .search-container {
    width: 100%;
  }

  #search-notes {
    width: 100%;
  }

  .notes-container {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  }

  app-title h1 {
    font-size: 2.2rem;
  }
}

@media screen and (max-width: 480px) {
  .notes-container {
    grid-template-columns: 1fr;
  }

  app-title h1 {
    font-size: 1.8rem;
  }
}

.form-title {
  color: var(--text-color);
  font-size: 1.8rem;
  font-weight: 800;
  margin-bottom: 10px;
  text-transform: uppercase;
}

/* animasi button */
button {
  position: relative;
  overflow: hidden;
}

button::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.2);
  transform: translateX(-100%);
  transition: transform 0.3s ease;
}

button:hover::after {
  transform: translateX(0);
}

/* animasi form */
.form-container input:focus,
.form-container textarea:focus {
  animation: wobble 0.5s ease;
}
